<template>
  <div class="error-detail-dialog">
    <!--  -->
    <div class="user-message-section">
      <div class="section-title">
        <AlertCircle class="icon" />
        <span></span>
      </div>
      <div class="user-message">
        {{ error.userMessage }}
      </div>
    </div>

    <!--  -->
    <div class="agent-info-section">
      <div class="info-row">
        <span class="label">:</span>
        <span class="value">{{ agentDisplayName }}</span>
      </div>
      <div class="info-row">
        <span class="label">:</span>
        <Tag :value="error.errorType" :severity="tagSeverity" />
      </div>
      <div class="info-row">
        <span class="label">:</span>
        <span class="value">{{ formattedTime }}</span>
      </div>
    </div>

    <!--  -->
    <Panel header="" toggleable collapsed class="technical-panel">
      <div class="technical-content">
        <!--  -->
        <div class="tech-section">
          <div class="tech-label">:</div>
          <pre class="tech-code">{{ error.technicalInfo?.detailedMessage || '' }}</pre>
        </div>

        <!--  -->
        <div class="tech-section">
          <div class="tech-label">:</div>
          <pre class="tech-code">{{ error.technicalInfo?.originalError || '' }}</pre>
        </div>

        <!-- HTTP  -->
        <div class="tech-section" v-if="error.technicalInfo?.technicalDetails?.status">
          <div class="tech-label">HTTP :</div>
          <pre class="tech-code">{{ error.technicalInfo.technicalDetails.status }}</pre>
        </div>

        <!--  -->
        <div class="tech-section" v-if="error.technicalInfo?.technicalDetails?.code">
          <div class="tech-label">:</div>
          <pre class="tech-code">{{ error.technicalInfo.technicalDetails.code }}</pre>
        </div>

        <!--  -->
        <div class="tech-section" v-if="error.technicalInfo?.technicalDetails?.type">
          <div class="tech-label">:</div>
          <pre class="tech-code">{{ error.technicalInfo.technicalDetails.type }}</pre>
        </div>

        <!--  -->
        <div class="tech-section" v-if="error.technicalInfo?.technicalDetails?.stack">
          <div class="tech-label">:</div>
          <pre class="tech-code stack-trace">{{ error.technicalInfo.technicalDetails.stack }}</pre>
        </div>

        <!--  -->
        <div class="tech-section" v-if="error.agentContext">
          <div class="tech-label">:</div>
          <pre class="tech-code">{{ JSON.stringify(error.agentContext, null, 2) }}</pre>
        </div>

        <!--  -->
        <div class="tech-section">
          <div class="tech-label">:</div>
          <pre class="tech-code">{{ JSON.stringify(error, null, 2) }}</pre>
        </div>
      </div>
    </Panel>

    <!--  -->
    <div class="action-buttons">
      <Button 
        label="" 
        icon="pi pi-copy" 
        severity="secondary" 
        @click="copyError"
        class="action-button"
      />
      <Button 
        label="" 
        @click="close"
        class="action-button"
      />
    </div>
  </div>
</template>

<script setup lang="ts">
/**
 * 
 * 
 * 
 * - 
 * - 
 * - 
 */
import { computed, inject, type Ref } from 'vue';
import Button from 'primevue/button';
import Tag from 'primevue/tag';
import Panel from 'primevue/panel';
import { AlertCircle } from 'lucide-vue-next';
import { useToast } from 'primevue/usetoast';
import type { DynamicDialogInstance } from 'primevue/dynamicdialogoptions';

const toast = useToast();

//  Dynamic Dialog 
const dialogRef = inject<Ref<DynamicDialogInstance>>('dialogRef');

const props = defineProps<{
  error: {
    agentId: string;
    errorType: string;
    errorCategory: string;
    timestamp: string;
    userMessage: string;
    technicalInfo?: {
      detailedMessage: string;
      originalError: string;
      errorName: string;
      technicalDetails: {
        status?: number;
        code?: string;
        type?: string;
        stack?: string;
      };
    };
    agentContext?: {
      agentName: string;
      roleId: string | null;
    };
  };
}>();

/**
 * 
 */
const agentDisplayName = computed(() => {
  const ctx = props.error.agentContext;
  if (ctx?.agentName && ctx.agentName !== props.error.agentId) {
    return `${ctx.agentName} (${props.error.agentId})`;
  }
  return props.error.agentId;
});

/**
 * 
 */
const formattedTime = computed(() => {
  try {
    const date = new Date(props.error.timestamp);
    return date.toLocaleString('zh-CN', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
      hour: '2-digit',
      minute: '2-digit',
      second: '2-digit'
    });
  } catch {
    return props.error.timestamp;
  }
});

/**
 * 
 */
const tagSeverity = computed(() => {
  const category = props.error.errorCategory;
  switch (category) {
    case 'auth':
    case 'server':
      return 'danger';
    case 'rate_limit':
    case 'network':
      return 'warning';
    case 'context_length':
      return 'info';
    default:
      return 'secondary';
  }
});

/**
 * 
 */
const copyError = async () => {
  try {
    const errorText = JSON.stringify(props.error, null, 2);
    await navigator.clipboard.writeText(errorText);
    toast.add({
      severity: 'success',
      summary: '',
      detail: '',
      life: 2000
    });
  } catch {
    toast.add({
      severity: 'error',
      summary: '',
      detail: '',
      life: 2000
    });
  }
};

/**
 * 
 */
const close = () => {
  dialogRef?.value?.close?.();
};
</script>

<style scoped>
.error-detail-dialog {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.user-message-section {
  background: var(--surface-100);
  border-radius: 8px;
  padding: 16px;
  border-left: 4px solid var(--red-500);
}

.section-title {
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: 600;
  color: var(--text-color);
  margin-bottom: 8px;
}

.icon {
  width: 20px;
  height: 20px;
  color: var(--red-500);
}

.user-message {
  font-size: 14px;
  line-height: 1.6;
  color: var(--text-color);
}

.agent-info-section {
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 12px;
  background: var(--surface-50);
  border-radius: 6px;
}

.info-row {
  display: flex;
  align-items: center;
  gap: 12px;
}

.label {
  font-size: 13px;
  color: var(--text-color-secondary);
  min-width: 80px;
}

.value {
  font-size: 13px;
  color: var(--text-color);
  font-family: monospace;
}

.technical-panel :deep(.p-panel-header) {
  font-size: 13px;
  padding: 12px;
}

.technical-content {
  display: flex;
  flex-direction: column;
  gap: 16px;
  padding: 8px 0;
}

.tech-section {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.tech-label {
  font-size: 12px;
  font-weight: 600;
  color: var(--text-color-secondary);
}

.tech-code {
  font-family: 'Courier New', monospace;
  font-size: 12px;
  background: var(--surface-100);
  padding: 12px;
  border-radius: 4px;
  overflow-x: auto;
  white-space: pre-wrap;
  word-break: break-word;
  color: var(--text-color);
  margin: 0;
  max-height: 200px;
  overflow-y: auto;
}

.stack-trace {
  max-height: 300px;
}

.action-buttons {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
  margin-top: 8px;
  padding-top: 16px;
  border-top: 1px solid var(--surface-200);
}

.action-button {
  font-size: 13px;
}
</style>
