<script setup lang="ts">
/**
 * 
 * 
 * 
 * 
 * 
 * @author Agent Society
 */
import { AlertTriangle } from 'lucide-vue-next';
import Button from 'primevue/button';
import Dialog from 'primevue/dialog';

const props = defineProps<{
  visible: boolean;
  title?: string;
  message?: string;
  confirmLabel?: string;
  cancelLabel?: string;
  confirmSeverity?: 'danger' | 'primary' | 'secondary';
  loading?: boolean;
}>();

const emit = defineEmits<{
  (e: 'update:visible', value: boolean): void;
  (e: 'confirm'): void;
  (e: 'cancel'): void;
}>();

const handleConfirm = () => {
  emit('confirm');
};

const handleCancel = () => {
  emit('cancel');
  emit('update:visible', false);
};
</script>

<template>
  <Dialog
    :visible="visible"
    @update:visible="(val: boolean) => emit('update:visible', val)"
    modal
    :closable="!loading"
    :close-on-escape="!loading"
    class="confirm-dialog"
    :pt="{
      root: { class: 'w-[360px] max-w-[90vw]' }
    }"
  >
    <template #container>
      <div class="confirm-container">
        <!--  -->
        <div class="confirm-header">
          <AlertTriangle class="w-4 h-4 text-red-500 flex-shrink-0" />
          <span class="text-sm font-medium text-[var(--text-1)]">{{ title || '' }}</span>
        </div>
        
        <!--  -->
        <div class="confirm-body">
          <p class="text-sm text-[var(--text-2)] leading-relaxed">
            {{ message || '' }}
          </p>
        </div>
        
        <!--  -->
        <div class="confirm-footer">
          <Button
            :label="cancelLabel || ''"
            variant="text"
            size="small"
            :disabled="loading"
            @click="handleCancel"
            class="!text-[var(--text-2)] !px-3 !py-1.5"
          />
          <Button
            :label="confirmLabel || ''"
            :severity="confirmSeverity || 'danger'"
            size="small"
            :loading="loading"
            @click="handleConfirm"
            class="!px-3 !py-1.5"
          />
        </div>
      </div>
    </template>
  </Dialog>
</template>

<style scoped>
/*  -  */
.confirm-container {
  background: var(--surface-1);
  border: 1px solid var(--border);
  border-radius: 10px;
  overflow: hidden;
}

/*  -  */
.confirm-header {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 20px;
  border-bottom: 1px solid var(--border);
  background: var(--surface-1);
}

/*  -  */
.confirm-body {
  padding: 24px 28px;
  background: var(--surface-1);
}

/*  -  */
.confirm-footer {
  display: flex;
  justify-content: flex-end;
  gap: 8px;
  padding: 12px 20px;
  border-top: 1px solid var(--border);
  background: var(--surface-1);
}
</style>
