/**
 *  API 
 * 
 *  LLM LLM 
 * 
 * @module services/configApi
 */

const BASE_URL = '/api';

/**
 *  HTTP 
 * @param endpoint - API 
 * @param options - 
 * @returns  JSON 
 * @throws 
 */
async function request<T>(endpoint: string, options?: RequestInit): Promise<T> {
  const response = await fetch(`${BASE_URL}${endpoint}`, {
    ...options,
    headers: {
      'Content-Type': 'application/json',
      ...options?.headers,
    },
  });

  if (!response.ok) {
    let detail = null;
    try {
      detail = await response.json();
    } catch {
      //  JSON 
    }
    const message = detail?.message || detail?.error || `HTTP : ${response.status}`;
    throw new Error(message);
  }

  return response.json();
}

/**
 * LLM 
 */
export interface LlmConfig {
  baseURL: string;
  model: string;
  apiKey: string;
  maxTokens: number;
  maxConcurrentRequests: number;
}

/**
 * 
 */
export interface ConfigStatus {
  hasLocalConfig: boolean;
  llmStatus: string;
  lastError: string | null;
}

/**
 *  API 
 */
export const configApi = {
  /**
   * 
   * 
   * : GET /api/config/status
   * 
   * @returns 
   * @throws 
   */
  async getConfigStatus(): Promise<ConfigStatus> {
    return request<ConfigStatus>('/config/status');
  },

  /**
   *  LLM 
   * 
   * : GET /api/config/llm
   *  app.json 
   * 
   * @returns LLM 
   * @throws 
   */
  async getLlmConfig(): Promise<{ llm: LlmConfig; source: string }> {
    return request<{ llm: LlmConfig; source: string }>('/config/llm');
  },

  /**
   *  LLM 
   * 
   * : POST /api/config/llm
   *  app.local.json
   * 
   * @param config - LLM 
   * @throws 
   */
  async saveLlmConfig(config: Partial<LlmConfig>): Promise<void> {
    await request<void>('/config/llm', {
      method: 'POST',
      body: JSON.stringify(config),
    });
  },

  /**
   *  LLM 
   * 
   * : GET /api/config/llm-services
   * 
   * @returns LLM 
   * @throws 
   */
  async getLlmServicesConfig(): Promise<{ services: any[]; source: string }> {
    return request<{ services: any[]; source: string }>('/config/llm-services');
  },

  /**
   *  LLM 
   * 
   * : POST /api/config/llm-services
   * 
   * @param service - 
   * @throws 
   */
  async addLlmService(service: any): Promise<void> {
    await request<void>('/config/llm-services', {
      method: 'POST',
      body: JSON.stringify(service),
    });
  },

  /**
   *  LLM 
   * 
   * : POST /api/config/llm-services/:serviceId
   * 
   * @param serviceId -  ID
   * @param service - 
   * @throws 
   */
  async updateLlmService(serviceId: string, service: any): Promise<void> {
    await request<void>(`/config/llm-services/${encodeURIComponent(serviceId)}`, {
      method: 'POST',
      body: JSON.stringify(service),
    });
  },

  /**
   *  LLM 
   * 
   * : DELETE /api/config/llm-services/:serviceId
   * 
   * @param serviceId -  ID
   * @throws 
   */
  async deleteLlmService(serviceId: string): Promise<void> {
    await request<void>(`/config/llm-services/${encodeURIComponent(serviceId)}`, {
      method: 'DELETE',
    });
  },
};
