import { defineStore } from 'pinia';
import { ref, computed } from 'vue';
import type { Agent } from '../types';
import { apiService } from '../services/api';

/**
 * 
 * 
 */
export const useAgentStore = defineStore('agent', () => {
  const agentsMap = ref<Record<string, Agent[]>>({});
  const allAgents = ref<Agent[]>([]); // 
  const loading = ref(false);
  const currentOrgId = ref<string | null>(null);

  /**
   * 
   */
  const fetchAllAgents = async (silent = false) => {
    if (!silent) loading.value = true;
    try {
      // getAgents('home')  /api/agents 
      //  orgId 
      const all = await apiService.getAgents('all'); 
      allAgents.value = all;
      return all;
    } catch (error) {
      console.error(':', error);
      return [];
    } finally {
      if (!silent) loading.value = false;
    }
  };

  /**
   *  ID 
   * @param orgId  ID
   * @param silent  ( loading )
   */
  const fetchAgentsByOrg = async (orgId: string, silent = false) => {
    currentOrgId.value = orgId;
    if (!silent) loading.value = true;
    try {
      // 1. 
      const fetchedAgents = await apiService.getAgents(orgId);
      
      // 
      const agentsToAdd: Agent[] = [];
      const existingIds = new Set(allAgents.value.map(a => a.id));
      
      fetchedAgents.forEach(agent => {
        if (!existingIds.has(agent.id)) {
          agentsToAdd.push(agent);
        }
      });
      
      if (agentsToAdd.length > 0) {
        allAgents.value = [...allAgents.value, ...agentsToAdd];
      }
      
      let result: Agent[] = [];
      if (orgId === 'home') {
        //  root  user
        result = fetchedAgents.filter(a => a.id === 'root' || a.id === 'user');
      } else {
        // 
        // 1.  user
        const userAgent = fetchedAgents.find(a => a.id === 'user');
        // 2. 
        const otherAgents = fetchedAgents
          .filter(a => a.id !== 'user')
          .sort((a, b) => (b.lastSeen || 0) - (a.lastSeen || 0));
        
        // 3. user 
        if (userAgent) result.push(userAgent);
        result.push(...otherAgents);
      }
      
      //  Vue 
      agentsMap.value = { ...agentsMap.value, [orgId]: result };
    } catch (error) {
      console.error(':', error);
      if (!agentsMap.value[orgId]) {
        agentsMap.value = { ...agentsMap.value, [orgId]: [] };
      }
    } finally {
      if (!silent) loading.value = false;
    }
  };

  /**
   *  ()
   */
  const agents = computed(() => {
    if (!currentOrgId.value) return [];
    return agentsMap.value[currentOrgId.value] || [];
  });

  /**
   * 
   */
  const agentCount = computed(() => agents.value.length);

  /**
   *  LLM 
   * @param agentId  ID
   */
  const abortAgent = async (agentId: string) => {
    try {
      await apiService.abortAgentLlmCall(agentId);
      // 
      if (currentOrgId.value) {
        await fetchAgentsByOrg(currentOrgId.value);
      }
    } catch (error) {
      console.error(':', error);
    }
  };

  return {
    agentsMap,
    allAgents,
    agents,
    loading,
    currentOrgId,
    fetchAgentsByOrg,
    fetchAllAgents,
    abortAgent,
    agentCount
  };
});
