/**
 *  API 
 * 
 * 
 *  org-templates API 
 * 
 * @module services/templateApi
 */

import type { OrgTemplate, TemplateContent } from '../types';

const BASE_URL = '/api';

/**
 *  HTTP 
 * @param endpoint - API 
 * @param options - 
 * @returns  JSON 
 * @throws 
 */
async function request<T>(endpoint: string, options?: RequestInit): Promise<T> {
  const response = await fetch(`${BASE_URL}${endpoint}`, {
    ...options,
    headers: {
      'Content-Type': 'application/json',
      ...options?.headers,
    },
  });

  if (!response.ok) {
    let detail = null;
    try {
      detail = await response.json();
    } catch {
      //  JSON 
    }
    const message = detail?.message || detail?.error || `HTTP : ${response.status}`;
    throw new Error(message);
  }

  return response.json();
}

/**
 *  API 
 */
export const templateApi = {
  /**
   * 
   * 
   *  org 
   *  org 
   * 
   * : GET /api/org-templates
   * : { templates: Array<{ orgName: string; infoMd: string }>, count: number }
   * 
   * @returns 
   * @throws 
   */
  async getTemplates(): Promise<OrgTemplate[]> {
    const data = await request<{ templates: Array<{ orgName: string; infoMd: string }>; count: number }>('/org-templates');
    //  { orgName, infoMd }  { id, name, description }
    return data.templates.map(t => ({
      id: t.orgName,
      name: t.orgName,
      description: t.infoMd?.substring(0, 100) || '', //  info.md 100
    }));
  },

  /**
   * 
   * 
   *  info.md  org.md 
   * 
   * 
   * : 
   * - GET /api/org-templates/:templateId/info
   * - GET /api/org-templates/:templateId/org
   * 
   * @param templateId -  ID
   * @returns  info.md  org.md 
   * @throws 
   */
  async getTemplateContent(templateId: string): Promise<TemplateContent> {
    const [infoData, orgData] = await Promise.all([
      request<{ orgName: string; infoMd: string }>(`/org-templates/${encodeURIComponent(templateId)}/info`),
      request<{ orgName: string; orgMd: string }>(`/org-templates/${encodeURIComponent(templateId)}/org`),
    ]);

    return {
      info: infoData.infoMd || '',
      org: orgData.orgMd || '',
    };
  },

  /**
   * 
   * 
   *  info.md  org.md 
   * 
   * :
   * - PUT /api/org-templates/:templateId/info
   * - PUT /api/org-templates/:templateId/org
   * 
   * @param templateId -  ID
   * @param content -  info.md  org.md 
   * @throws 
   */
  async saveTemplateContent(templateId: string, content: TemplateContent): Promise<void> {
    await Promise.all([
      request<void>(`/org-templates/${encodeURIComponent(templateId)}/info`, {
        method: 'PUT',
        body: JSON.stringify({ content: content.info }),
      }),
      request<void>(`/org-templates/${encodeURIComponent(templateId)}/org`, {
        method: 'PUT',
        body: JSON.stringify({ content: content.org }),
      }),
    ]);
  },

  /**
   * 
   * 
   *  org  info.md  org.md
   * 
   * : POST /api/org-templates
   * 
   * @param orgName - 
   * @returns 
   * @throws 
   */
  async createTemplate(orgName: string): Promise<{ ok: boolean; orgName: string }> {
    return request<{ ok: boolean; orgName: string }>('/org-templates', {
      method: 'POST',
      body: JSON.stringify({ orgName }),
    });
  },

  /**
   * 
   * 
   *  org 
   * 
   * : DELETE /api/org-templates/:templateId
   * 
   * @param templateId -  ID
   * @throws 
   */
  async deleteTemplate(templateId: string): Promise<{ ok: boolean; orgName: string }> {
    return request<{ ok: boolean; orgName: string }>(`/org-templates/${encodeURIComponent(templateId)}`, {
      method: 'DELETE',
    });
  },
};
